<template>
<!--   -->
    <div class="Search">
        <div class="Da-search">

            <div class="searchBox">
                <div class="return-icon" @click="calendar">
                    <img src="@/assets/return.png" />
                </div>
                <input class="inp" placeholder="" v-model="value" />
                <div class="calendar" @click="souClick('')" v-if="!showMes"></div>
                <div class="calendar" @click="souClick('')" v-else></div>
            </div>
        </div>

        <!--   -->
        <div class="content" v-if="showMes">
            <MessagelistNewest v-if="storelist.length>0" class="Messagelist" :storelist="storelist" />
            <div class="not" v-else></div>
        </div>


    </div>
</template>
<script>
import MessagelistNewest from '@/components/MessagelistNewest';

import {GetIdByList} from '@/api/message';
import {mapGetters} from 'vuex'

export default {
    name:'MessageSou',
    data () {
        return {
            value:'',
            showMes:false,
            storelist:[
                {
                    img_url:require('../assets/Message-1.png'),
                    title:'',
                    desc:'',
                    conceal:1
                },
                {
                    img_url:require('../assets/Message-2.png'),
                    title:'',
                    desc:'',
                    conceal:2
                },
                {
                    img_url:require('../assets/Message-1.png'),
                    title:'',
                    desc:'',
                    conceal:3
                },
                {
                    img_url:require('../assets/Message-2.png'),
                    title:'',
                    desc:'',
                    conceal:4
                },
                {
                    img_url:require('../assets/Message-1.png'),
                    title:'',
                    desc:'',
                    conceal:5
                }
            ]
        }
    },
    computed: {
        ...mapGetters([
            'userInfo'
        ])
    },
    components:{
        MessagelistNewest
    },
    methods:{
        calendar(){
            this.$router.go(-1);//
        },
        souClick(e) {
            if(e===''){

                var temp={
                    id:this.userInfo.openid,
                    value:this.value
                }
                GetIdByList(temp).then(res=>{
                    console.log(res)
                    this.storelist=res.data
                })
                this.showMes = true
            }
            else{
                this.showMes = false
                this.value = ''
            }


        }
        // toggleResult() {
        //     this.$router.push({path:'/message/messageResult'})
        // }
    }
}
</script>
<style lang="scss" scoped>
.Search{
    .Da-search{
        z-index: 10;
        background-color: #ffffff;
        padding-top: .2rem;
        padding-bottom: .2rem;
        position: fixed;
        left: 0;
        right: 0;
        top: 0;
        .searchBox{
            width: 9.15rem;
            margin:0 auto;
            display: flex;
            justify-content: space-between;
            height: 0.75rem;
            align-items: center;
            .return-icon{
                width: 0.7rem;
                height: 0.7rem;

                img{
                    width: 100%;
                    height: 100%;
                }
            }
            .inp{
                    border-radius:0.41rem;
                    padding-left: .5rem;
                    font-size:0.32rem;
                    width:7rem;
                    height:0.83rem;
                    line-height: 0.83rem;
                    font-size:0.4rem;
                    border: 0;
                    font-family:Source Han Sans CN;
                    font-weight:400;
                    color: #000000;
                    background: #f1f1f1;
                }
                input::-webkit-input-placeholder{
                    color: #999999;
                }
            .calendar{
                font-size: 0.4rem;
                font-weight:400;
                color: #666666;
            }
        }
    }
    .content{
        margin-top: 1.2rem;
        .not{
            text-align: center;
            margin-top: 1.5rem;
            font-size:0.4rem;
            font-family:Source Han Sans CN;
            font-weight:400;
            color: #666666;
        }
    }
}
</style>
