import Vue from 'vue'
import VueRouter from 'vue-router'
import Workbench from '../views/Workbench.vue'
import Order from '../views/Order.vue'
import Datas from '../views/Datas.vue'
import Search from '../views/Search.vue'
import Orderdetails from '../views/Orderdetails.vue'
import Message from '../views/Message.vue'
import MessageSou from '../views/MessageSou.vue'
import Demand from '../views/Demand.vue'
import Demandadd from '../views/Demandadd.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Workbench',
    component: ()=> import('../views/Workbench.vue'),
    // meta:{
    //   title: ''
    // }
  },
    {
        path: '/:id(\\d+)',
        name: 'edit',
        component: ()=> import('../views/Workbench.vue'),
        // meta: {
        //   title: ''
        // }
    },
  {
    path: '/order',
    name: 'Order',
    component: ()=> import('../views/Order.vue'),
    // meta:{
    //   title: ''
    // }
  },

  {
    path: '/order/orderhistory',
    name: 'OrderHistory',
    component: ()=> import('../views/OrderHistory.vue'),
    // meta:{
    //   title: ''
    // }
  },
  {
    path: '/order/install',
    name: 'Install',
    component: ()=> import('../views/Install.vue'),
    // meta:{
    //   title: ''
    // }
  },
  {
    path: '/order/product',
    name: 'Product',
    component: ()=> import('../views/Product.vue'),
    // meta:{
    //   title: ''
    // }
  },
  {
    path: '/offorder/offordersou',
    name: 'Offordersou',
    component: ()=> import('../views/Offordersou.vue'),
    // meta:{
    //   title: ''
    // }
  },
  {
    path: '/datas',
    name: 'Datas',
    component: ()=> import('../views/Datas.vue'),
    // meta:{
    //   title: ''
    // }
  },
  {
    path: '/search',
    name: 'Search',
    component: ()=> import('../views/Search.vue'),
    // meta:{
    //   title: ''
    // }
  },
  {
    path: '/order/Orderdetails',
    name: 'Orderdetails',
    component: ()=> import('../views/Orderdetails.vue'),
    // meta:{
    //   title: ''
    // }
  },
  {
    path: '/message',
    name: 'Message',
    component: ()=> import('../views/Message.vue'),
    // meta:{
    //   title: ''
    // }
  },
  {
    path: '/message/messageSou',
    name: 'MessageSou',
    component: ()=> import('../views/MessageSou.vue'),
    // meta:{
    //   title: ''
    // }

  },
  {
    path: '/demand',
    name: 'Demand',
    component: ()=> import('../views/Demand.vue'),
    // meta:{
    //   title: ''
    // }
  },
  {
    path:'/demandadd',
    name:'Demandadd',
    component: ()=> import('../views/Demandadd.vue'),
    // meta:{
    //   title: ''
    // }

  }
]

const router = new VueRouter({
  // mode: 'history',
  base: process.env.BASE_URL,
  routes,
  scrollBehavior(to,from) {
    return{ x:0, y:0}
  }
})

router.beforeEach((to, from, next) => {

  if(to.meta.title) {
    document.title = to.meta.title;
    next()
  }else {
    next()
  }
})

export default router
